import { withRouter } from 'next/router'
import { Row, Col, List } from 'antd'
import { Item } from 'rc-menu'
import Link from 'next/link'
import Router from 'next/router'

const api = require('../lib/api')

const LANGUAGES = ['JavaScript', 'HTML', 'CSS', 'TypeScript', 'JAVA', 'Python']
const SORT_TYPES = [
  {
    name: 'Best Match'
  },
  {
    name: 'Most Stars',
    value: 'stars',
    order: 'desc'
  },
  {
    name: 'Fewest Stars',
    value: 'stars',
    order: 'asc'
  },
  {
    name: 'Most Forks',
    value: 'forks',
    order: 'desc'
  },
  {
    name: 'Fewest Forks',
    value: 'forks',
    order: 'asc'
  },
]

const selectedItemStyle = {
  borderLeft: '2px solid #e36209',
  fontWeight: 100,
}

/**
 * 
 * sort: 排序方式
 * order: 排序顺序
 * lang: 仓库的项目开发主语言
 * page: 分页页面
 */

function Search({router, repos}) {
  console.log(repos)

  const { sort, order, lang, query } = router.query

  const handleLauguageChange = (language) => {
    Router.push({
      pathname: '/search',
      query: {
        query,
        lang: language,
        sort,
        order,
      }
    })
  }

  const handleSortChange = (sort) => {
    Router.push({
      pathname: '/search',
      query: {
        query,
        lang,
        sort: sort.value,
        order: sort.order,
      }
    })
  }
  return (
    <div className='root'>
      <Row gutter={20}>
        <Col span={6}>
          <List
            bordered
            header={<span className="list-header">Language</span>}
            style={{ marginBottom: 20 }}
            dataSource={LANGUAGES}
            renderItem={item => {
              const selected = lang === item

              return (
                <List.Item style={selected ? selectedItemStyle : null}>
                  <a onClick = {() => handleLauguageChange(item)}>{item}</a>
                </List.Item>
              )
            }}
          />
          <List
            bordered
            header={<span className="list-header">Sort</span>}
            style={{ marginBottom: 20 }}
            dataSource={SORT_TYPES}
            renderItem={item => {
              const selected = false
              if (ite.name === 'Best Match' && !sort) {
                selected = true
              } else if (item.value === sort && item.order === order) {
                selected = true
              }
              return (
                <List.Item style={selected ? selectedItemStyle : null}>
                  <a onClick = {() => handleLauguageChange(item)}>{item.name}</a>
                </List.Item>
              )
            }}
          />
        </Col>
      </Row>
    </div>
  )
}

Search.getInitialProps = async ({ ctx }) => {
  const { query, sort, lang, order, page } = ctx.query

  if (!query) {
    return {
      repos: {
        total_count: 0
      }
    }
  }

  // ?q=react+language:javascript&sort=star&order=desc&page=2

  let queryString = `?q=${query}`
  if (lang) queryString += `language:${lang}`
  if (sort) queryString += `&sort=${sort}&order=${order || 'desc'}`
  if (page) queryString += `&page=${page}`
  console.log(queryString);
  const result = await api.request({
    url: `/search/repositories${queryString}`
  }, ctx.req, ctx.res)

  return {
    repos: result.data
  }
}

export default withRouter(Search)